.flex-mobile-desktop{
  @include media($mediapoint-very-small, $mediapoint-medium) {
    display: flex;
    flex-direction: column;
    & > div:nth-child(1), {
      order: 1;
      margin-top: 50px;
    }
    & > div:nth-child(2){
      order: 2;
     // margin-top: 100px;
    }
  }
}


.menu-container {
  margin-top: 40px;
  background: linear-gradient(353.35deg, #F7D523 -17.99%, #F7BC23 57.36%);
  width: 100%;
  height: 80px;
  &.no-menu-container{
    display: none;
  }
  @include media($mediapoint-small, $mediapoint-medium) {
    margin-top: 110px;
    bottom: 0;
    z-index: 1000;
  }
  @include media($mediapoint-very-small, $mediapoint-small) {
    margin-top: 130px;
    bottom: 0;
    z-index: 1000;
  }

  .animation-menu {
    background: linear-gradient(353.35deg, #F7D523 -17.99%, #F7BC23 57.36%);
    height: 80px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding-left: 0px;
    position: relative;
    @include media($mediapoint-small, $mediapoint-large) {
      padding-left: 10px;

    }
    @include media($mediapoint-very-small, $mediapoint-small) {

      justify-content: center;
      padding-left: 0px;
      width: 320px;
      margin: 0 auto;
    }

    &-active#main-information {

      position: absolute;
      top: 17px;
      left: 0;
      border-radius: 12px;
      width: 74px;
      height: 48px;
      background: #1D1D1B;
      box-shadow: 0px 4px 14px rgba(29, 29, 27, 0.32);
      transition: all 0.4s ease-in-out;

      &[data-active="left"] {
        left: 0px;
        width: 175px;

      }
      &[data-active="middle"] {
        left: 175px;
        width: 230px;

      }

      &[data-active="right"] {
        left: 405px;
        width: 125px;

      }
      @include media($mediapoint-small, $mediapoint-large) {
        &[data-active="left"] {
          left: 15px;

        }
        &[data-active="middle"] {
          left: 200px;
        }

        &[data-active="right"] {
          left: 440px;
        }
      }
      @include media($mediapoint-very-small, $mediapoint-small) {
        border-radius: 50%;
        width: 74px !important;
        height: 74px;
        top: 20px;
        background: #1D1D1B;
        box-shadow: 0px 4px 14px rgba(29, 29, 27, 0.32);
        margin-top: -10px;
        transition: all 0.4s ease-in-out;
        &:before {
          content: '';
          height: 100px;
          width: 100px;
          background-color: #f6c923;
          position: absolute;
          top: -10px;
          left: -13px;
          z-index: -1;
          border-radius: 50%;
        }
        &[data-active="left"] {
          left: 28px;

        }
        &[data-active="middle"] {
          left: 122px;

        }
        &[data-active="right"] {
          left: 215px;

        }
      }

    }
    &-active#requests {

      position: absolute;
      top: 17px;
      left: 0;
      border-radius: 12px;
      width: 74px;
      height: 48px;
      background: #1D1D1B;
      box-shadow: 0px 4px 14px rgba(29, 29, 27, 0.32);
      transition: all 0.4s ease-in-out;

      &[data-active="left"] {
        left: 0px;
        width: 165px;

      }
      &[data-active="middle"] {
        left: 165px;
        width: 160px;

      }

      &[data-active="right"] {
        left: 320px;
        width: 145px;

      }
      @include media($mediapoint-small, $mediapoint-large) {
        &[data-active="left"] {
          left: 15px;

        }
        &[data-active="middle"] {
          left: 185px;

        }

        &[data-active="right"] {
          left: 355px;
        }
      }
      @include media($mediapoint-very-small, $mediapoint-small) {
        border-radius: 50%;
        width: 74px !important;
        height: 74px;
        top: 20px;
        background: #1D1D1B;
        box-shadow: 0px 4px 14px rgba(29, 29, 27, 0.32);
        margin-top: -10px;
        transition: all 0.4s ease-in-out;
        &:before {
          content: '';
          height: 100px;
          width: 100px;
          background-color: #f7bc23;
          position: absolute;
          top: -10px;
          left: -13px;
          z-index: -1;
          border-radius: 50%;
        }
        &[data-active="left"] {
          left: 28px;

        }
        &[data-active="middle"] {
          left: 122px;

        }
        &[data-active="right"] {
          left: 217px;

        }
      }

    }
    &-active#documents {
      position: absolute;
      top: 17px;
      left: 0;
      border-radius: 12px;
      width: 74px;
      height: 48px;
      background: #1D1D1B;
      box-shadow: 0px 4px 14px rgba(29, 29, 27, 0.32);
      transition: all 0.4s ease-in-out;

      &[data-active="left"] {
        left: 0px;
        width: 220px;

      }
      &[data-active="middle"] {
        left: 215px;
        width: 120px;

      }

      @include media($mediapoint-small, $mediapoint-large) {
        &[data-active="left"] {
          left: 20px;

        }
        &[data-active="middle"] {
          left: 245px;

        }

      }
      @include media($mediapoint-very-small, $mediapoint-small) {
        border-radius: 50%;
        width: 74px !important;
        height: 74px;
        top: 20px;
        background: #1D1D1B;
        box-shadow: 0px 4px 14px rgba(29, 29, 27, 0.32);
        margin-top: -10px;
        transition: all 0.4s ease-in-out;
        &:before {
          content: '';
          height: 100px;
          width: 100px;
          background-color: #f7c723;
          position: absolute;
          top: -10px;
          left: -13px;
          z-index: -1;
          border-radius: 50%;
        }
        &[data-active="left"] {
          left: 75px;

        }
        &[data-active="middle"] {
          left: 172px;

        }
      }

    }

    &_tab {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      height: 48px;
      padding: 0 12px;
      margin-right: 7px;
      cursor: pointer;
      -webkit-tap-highlight-color: transparent;
      z-index: 100;
      @include media($mediapoint-small, $mediapoint-large) {
        padding: 0 20px;
        margin-right: 0px;
      }
      @include media($mediapoint-very-small, $mediapoint-small) {
        flex-direction: column;
        width: 74px;
        height: 74px;
        padding: 0 0px;
        justify-content: center;
        margin: 0 10px;
      }

      &.active {
        @include media($mediapoint-very-small, $mediapoint-small) {
          margin-top: 10px;
        }
        p {
          color: #F7A823;
          @include media($mediapoint-very-small, $mediapoint-small) {
            display: none;
          }
        }

        svg {
          path {
            fill: #F7A823;

          }
        }
      }

      svg {
        margin-right: 10px;
        @include media($mediapoint-very-small, $mediapoint-small) {
          margin-right: 0px;
        }

        path {
          fill: #1D1D1B;

        }
      }

      p {
        /* Body/Body L Regular */

        font-family: Manrope;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;
        /* identical to box height, or 125% */

        display: flex;
        align-items: center;

        /* Achromatic/Black Primary */

        color: #1D1D1B;
        @include media($mediapoint-very-small, $mediapoint-small) {
          font-family: Manrope;
          font-style: normal;
          font-weight: bold;
          font-size: 12px;
          line-height: 16px;
          margin: 5px 0;
          text-align: center;
        }
      }
    }
  }


}
.animation-menu-text {
  display: none;
  min-height: 60px;
  &.animation-menu-text-no-margin{
    @include media($mediapoint-very-small, $mediapoint-small) {
      margin: 0 0px !important;
      padding-top: 20px;
      padding-bottom: 0px;
    }
  }
  @include media($mediapoint-very-small, $mediapoint-small) {
    margin: 0 20px !important;
  }
  span {
    color: #56AF31;
    &.red-text{
      color: #E74C3C !important;
    }
  }

  &.active {
    display: block;

  }
}
.information-block{
  padding-top: 30px;
  min-height: 400px;
  padding-bottom: 50px;
  @include media($mediapoint-medium, $mediapoint-large) {
    padding: 30px 20px !important;
  }
  @include media($mediapoint-small, $mediapoint-medium) {
    padding: 30px 20px!important;
  }
  @include media($mediapoint-very-small, $mediapoint-small) {
    padding-top: 0px;
  }
  &-bg{
    @include media($mediapoint-very-small, $mediapoint-small) {
      margin: 25px 0px 0px 0px!important;
      padding-top: 25px;
      background: #f0f0f0;
      border-radius: 12px;
      padding-bottom: 0px;
      h2{
        margin: 0px auto !important;
      }
    }
  }
  h2{
    font-family: Manrope;
    font-style: normal;
    font-weight: bold;
    font-size: 28px;
    line-height: 40px;
    color: #1D1D1B;
    @include media($mediapoint-very-small, $mediapoint-small) {
      font-size: 20px;
      line-height: 28px;
      text-align: center;
      margin: 25px auto;
    }
  }
  h3{
    /* Headers/H1 Header */

    font-family: Manrope;
    font-style: normal;
    font-weight: bold;
    font-size: 21px;
    line-height: 23px;
    /* identical to box height, or 143% */
    margin-bottom: 20px;

    /* Achromatic/Black */

    color: #1D1D1B;
    @include media($mediapoint-very-small, $mediapoint-small) {
      font-size: 20px;
      line-height: 28px;
      margin: 25px auto;
      text-align: center;
    }
  }
  &_content{
    max-width:736px;
    &-w-100{
      max-width:2000px !important;
    }
    span{
      color: #1D1D1B ;
    }
    &.flex-width-100{
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
      max-width: 1200px;
      flex-wrap: wrap;
    }
    &.flex-width-margin{
    margin: 20px 20px !important;
    }
    p{

      font-family: Manrope;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 20px;
      /* or 125% */


      /* Achromatic/Black */

      color: #1D1D1B;

    }
    a, button{
      margin: 50px 0 0 0;
      @include media($mediapoint-very-small, $mediapoint-small) {
        margin:0 auto;
      }

    }
    a{
      @include media($mediapoint-very-small, $mediapoint-small) {
        //padding: 7px 0;
        display: block;
      }

    }
    .forms-block{
      padding: 0;
       width: 736px;
    }
    .forms-block-100{
      padding: 0;
      width: 1200px;
    }
    &.flex-blocks-wrap{
      max-width: 1200px;
      display: flex;
      flex-direction: row;
      justify-content: start;
      align-items: center;
      flex-wrap: wrap;
      margin: 0px -10px 50px -10px;
      >a{
        background: #F0F0F0;
        border-radius: 12px;
        text-decoration: none;
        height: 72px;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        cursor: pointer;
        padding: 2px 15px;
        min-width: 280px;
        width: 29%;
        margin: 10px;
        @include media($mediapoint-very-small, $mediapoint-medium ) {
          height: auto;
        }
        &:hover{
          background-color: #BAE7EC ;
        }
        img{
          margin-right: 10px;
        }
        p{
          /* Body/Body L Bold */

          font-family: Manrope;
          font-style: normal;
          font-weight: bold;
          font-size: 16px;
          line-height: 20px;
          /* or 125% */
          margin: 10px 0;
          display: flex;
          align-items: center;

          /* Chromatic/Blue Primary */

          color: #0094AA;
        }
        @include media($mediapoint-medium, $mediapoint-large) {
         width: 44%;
        }
        @include media($mediapoint-small, $mediapoint-medium) {
          width: 90%;
        }
        @include media($mediapoint-very-small, $mediapoint-small) {
          width: 100%;
          background: inherit;
          margin: 0px 0;
        }
      }
    }
  }
}
.accordion{
  max-width:736px;
  margin-bottom: 25px;
  .ui-accordion-header{
    //min-height: 44px;
    /* Body/Body XL Bold */
    cursor: pointer;
    font-family: Manrope;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 28px;
    /* identical to box height, or 140% */
    margin: 0;

    /* Achromatic/Black Primary */
    //border-bottom: 1px solid #F0F0F0;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
    color: #1D1D1B;
    padding: 20px 40px 15px 12px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    position: relative;
    @include media($mediapoint-very-small, $mediapoint-small) {
      font-size: 16px;
      line-height: 20px;
      padding: 15px 40px 15px 7px;
      text-align: left;
      border-bottom: 1px solid #f0f0f0;

    }
    &:focus, &:active {outline:none !important; border-bottom: 0 !important; margin-top: 10px !important;
    }
    .header-counter{
      margin-right: 10px;
    }
    &:before{
      content: ' ';
      text-align: center;
      line-height: 42px;
      color: #fff;
      background: url("../../../images/arrow.svg") 0 0 no-repeat;
      position: absolute;
      top: 44%;
      right: 20px;
      height: 10px;
      width: 15px;
    }
    &.ui-accordion-header-active{
      &:before{
        transform: matrix(1, 0, 0, -1, 0, 0);
      }
    }
    span{
      color: #1D1D1B;

    }
    &.ui-accordion-header-active{
      border-bottom-left-radius: 0px;
      border-bottom-right-radius: 0px;
      background-color: #F0F0F0;
      border-bottom: 0px solid grey;
      margin: 0;
    }
  }
  .ui-accordion-content{
    /* Note: backdrop-filter has minimal browser support */
    background-color: #F0F0F0;
    border-bottom: 0px solid grey;
    border-bottom-left-radius:16px;
    border-bottom-right-radius: 16px;
    border-top: 0px solid grey !important;
    color: white;
    padding: 0em 20px 15px 20px !important;
    min-height: 50px;
    margin-bottom: 10px;
    -webkit-tap-highlight-color: transparent;
    p{
      font-family: Manrope;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 20px;
      color: #1D1D1B;
      margin: 0;
      padding: 5px 40px 5px 0px;
      @include media($mediapoint-very-small, $mediapoint-medium) {
        padding: 5px 0px 5px 0px;
        font-size: 14px;
        line-height: 20px;
      }
    }

  }
  ul{
    padding: 0 0px;
    li{
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items:flex-start;
      list-style: none !important;
      font-family: Manrope;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 20px;
      /* or 125% */
      /* Achromatic/Black Primary */
      color: #1D1D1B;
      margin: 0;
      padding: 5px 40px 5px 0px;
      @include media($mediapoint-very-small, $mediapoint-medium) {
        padding: 5px 0px 5px 0px;
        font-size: 14px;
        line-height: 20px;
      }
      .list-bold {
        display: block;
        margin: 5px 10px;
      }
      span{
        color: #1D1D1B;
      }
      span:nth-child(1){
        margin-right: 5px;
      }
    }
  }
}