//классы шаблоны
%d-flex-row {
  display: flex;
  flex-direction: row;
}
%d-flex-col {
  display: flex;
  flex-direction: column;
}
%d-flex-center-center {
  align-items: center;
  justify-content: center;
}
%d-flex-between-center {
  align-items: center;
  justify-content: space-between;
}
%d-flex-start-center {
  align-items: center;
  justify-content: flex-start;
}
%d-flex-end-center {
  align-items: center;
  justify-content: flex-end;
}
$mediapoint-very-small: 0;
$mediapoint-small-360: 360;
$mediapoint-small: 575;
$mediapoint-medium: 768;
$mediapoint-medium-medium: 833;
$mediapoint-medium-large: 992;
$mediapoint-large: 1275;

@mixin media($width-1, $width-2) {
  @if ($width-1 != $width-2) {
    @media (min-width: #{$width-1 + 1 + 'px'}) and (max-width: #{$width-2 + 'px'}) {
      @content;
    }
  }
  @if ($width-1 == $width-2) {
    @media (min-width: #{$width-1 + 1 + 'px'}) {
      @content;
    }
  }
}
