
/*Шаблон для форм*/
.forms-block{
  padding: 0 0 30px 0 !important;
  @include media($mediapoint-very-small, $mediapoint-medium ) {
    padding: 0 0 0px 0 !important;
  }
  &_title{
    font-family: Manrope;
    font-style: normal;
    font-weight: bold;
    font-size: 28px;
    line-height: 40px;
    color: #1D1D1B;
    @include media($mediapoint-very-small, $mediapoint-medium ) {
      border-top-left-radius: 12px;
      border-top-right-radius: 12px;
      background: #F0F0F0;
      margin: 0;
      padding: 10px 25px;
    }

  }
  h3{
    /* Body/Body XL Bold */
    margin: 10px 0;
    font-family: Manrope;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 28px;
    /* identical to box height, or 140% */


    /* Achromatic/Black Primary */

    color: #1D1D1B;
  }
  p.description{
    /* Body/Body L Regular */

    font-family: Manrope;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    color: #1D1D1B;
    @include media($mediapoint-very-small, $mediapoint-small) {
      text-align: center;
    }
  }
  &_form{
    background: #F0F0F0;
    border-radius: 12px;
    max-width: 760px;
    min-height: 220px;
    padding: 25px;
    position: relative;
    &.form-100{
      max-width: 1200px !important;
    }
    @include media($mediapoint-very-small, $mediapoint-medium ) {
      &.white-mobile-bg{
        background: white;
      }

    }
    input:focus,
    textarea:focus {
      outline: none !important;
      border: 1px solid #56AF31 !important;
      color: #4F4F4F;
      background-color: #DFF4F7;
    }
    &-input{
      position: relative;
      @include media($mediapoint-very-small, $mediapoint-small ) {
        margin-bottom: 20px;
      }
      label{
        font-family: Manrope;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
        color: #1D1D1B;
        width: 100%;
        display: block;
        text-align: left;
        padding: 5px 0 5px 15px;
        @include media($mediapoint-very-small, $mediapoint-small ) {
          text-align: center;
          padding: 5px 0 5px 0px !important;
        }

      }
      input{
        /* Achromatic/White */
        height: 48px;
        background: #FFFFFF;
        /* Achromatic/Gray 0D */
        border: 1px solid #E0E0E0;
        box-sizing: border-box;
        /* Input Shadow */
        width: 100%;
        padding: 0 15px;
        box-shadow: 0px 4px 12px rgba(29, 29, 27, 0.04);
        border-radius: 12px;
        /* Body/Body XL Regular */
        color: #A7A7A7;
        font-family: Manrope;
        font-style: normal;
        font-weight: normal;
        font-size: 20px;
        line-height: 28px;
        @include media($mediapoint-very-small, $mediapoint-small ) {
          text-align: center;
        }
      }
      svg{
        position: absolute;
        top: 40px;
        right: 15px;
      }
    }
    &-select{
      position: relative;
      label{
        font-family: Manrope;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
        color: #1D1D1B;
        width: 100%;
        display: block;
        text-align: left;
        padding: 5px 0 5px 15px;
        @include media($mediapoint-very-small, $mediapoint-medium ) {
          text-align: center;
          padding: 5px 0 5px 0px !important;
        }

      }
      select{
        /* Achromatic/White */
        height: 48px;
        background: #FFFFFF;
        /* Achromatic/Gray 0D */
        border: 1px solid #E0E0E0;
        box-sizing: border-box;
        /* Input Shadow */
        width: 100%;
        padding: 0 15px;
        box-shadow: 0px 4px 12px rgba(29, 29, 27, 0.04);
        border-radius: 12px;
        /* Body/Body XL Regular */
        color: #A7A7A7;
        font-family: Manrope;
        font-style: normal;
        font-weight: normal;
        font-size: 20px;
        line-height: 28px;
        outline:none;
        @include media($mediapoint-very-small, $mediapoint-small ) {
          text-align: center;
        }
        option{
          color: #1D1D1B;
          background: #FFFFFF;
          /* Achromatic/Gray 0D */

          border: 1px solid #E0E0E0;
          box-sizing: border-box;
          /* Shadows/Input Default Shadow */

          box-shadow: 0px 4px 12px rgba(29, 29, 27, 0.04);
          border-radius: 12px;
        }
      }
      svg{
        position: absolute;
        top: 40px;
        right: 15px;
      }
    }
    &-textarea{
      width: 100%;
      label{
        font-family: Manrope;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
        color: #1D1D1B;
        width: 100%;
        display: block;
        text-align: left;
        padding: 5px 0 5px 15px;
        @include media($mediapoint-very-small, $mediapoint-medium ) {
          text-align: center;
          padding: 5px 0 5px 0px !important;
        }
      }
      textarea{
        /* Achromatic/White */
        height: 150px;
        background: #FFFFFF;
        /* Achromatic/Gray 0D */
        border: 1px solid #E0E0E0;
        box-sizing: border-box;
        /* Input Shadow */
        width: 100%;
        padding: 10px 15px;
        box-shadow: 0px 4px 12px rgba(29, 29, 27, 0.04);
        border-radius: 12px;
        /* Body/Body XL Regular */
        color: #A7A7A7;
        font-family: Manrope;
        font-style: normal;
        font-weight: normal;
        font-size: 20px;
        resize: none;
        line-height: 28px;
        @include media($mediapoint-very-small, $mediapoint-small ) {
          text-align: center;
        }
      }
    }
    button, .button-form{
      /* Chromatic/Green Primary */
      width: 260px !important;
      height: 44px;
      background: #56AF31;
      /* Primary Button Shadow */
      display: flex;
      align-items: center;
      text-align: center;
      box-shadow: 0px 4px 14px rgba(80, 163, 46, 0.32);
      border-radius: 24px;
      font-family: Manrope;
      -webkit-tap-highlight-color: transparent;
      font-style: normal;
      font-weight: 800;
      font-size: 16px;
      line-height: 20px;
      color: #FFFFFF;
      justify-content: center;
      border: 0px;
      margin: 25px 0;
      cursor: pointer;
      @include media($mediapoint-very-small, $mediapoint-small) {
        margin: 0px auto !important;
      }
      &:hover {
        background-color: #7BD057;
      }

      &:focus {
        background-color: #3F8024;
      }
    }
    .choose-time-call{
      max-width: 470px;
      @include media($mediapoint-very-small, $mediapoint-medium) {
        margin: 0 auto;
      }
      &_input{
        text-align: center;
        background: #E0E0E0;
        border-radius: 12px;
        height: 80px;
        display: flex;
        align-items: center;

        justify-content: center;
        margin-top: 10px;
        pointer-events: none;
        &.active{
          background: #D0F1C1;
          pointer-events: initial;
          input{
            background: #FFFFFF;
          }
        }
        input{
          width: 50%;
          background: #F0F0F0;
          text-align: center;
        }
      }
      &_choose{
        display: flex;
        align-items: center;
        text-align: center;
        justify-content: space-between;
        flex-wrap: wrap;


        &-box{
          background: #E0E0E0;
          border-radius: 12px;
          height: 48px;
          width: 226px;
          margin: 10px 0;
          cursor: pointer;
          @include media($mediapoint-very-small, $mediapoint-medium) {
            width: 48%;
          }
          &.active{
            background: #56AF31;
            p{
              color: white;
            }
          }
          &:hover{
            background: #A7A7A7;
            p{
              color: white;
            }
          }
          p{
            /* Body/Body L Regular */

            font-family: Manrope;
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 16px;
            /* identical to box height, or 125% */

            text-align: center;

            /* Achromatic/Gray 2 */

            color: #4F4F4F;

          }
        }
      }
    }

  }
  .row-input{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: space-between;
    @include media($mediapoint-very-small, $mediapoint-small) {
      flex-direction: column;
      padding: 0 0px;
      &.row-count-2 > div{
        width: 100% !important;

      }
    }
    &.row-margin{
      margin-bottom: 15px;
    }

    &.row-count-2 > div{
      width: 48%;
    }
    &.row-count-1 > div{
     // width: 100%;
    }
  }
}

.breadcrumb{
  margin-left: 5px;
  &:hover {
    svg path{
      fill: $blue-hover;
    }
    p{
      color: $blue-hover;
    }
  }
  &_row{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: flex-start;
  }
  svg{
    transform: rotate(90deg);
    path{
      fill: $blue;
    }
  }
  a{
    text-decoration: none;
  }
  p{
    font-family: Manrope;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    color: $blue;
    margin-left: 15px;
  }
}

.contact-information{
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: flex-start;

  @include media($mediapoint-small, 1135) {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    text-align: center;
    justify-content: flex-start;
  }
  @include media($mediapoint-very-small, $mediapoint-small) {
    width: 100%;
    justify-content: center;
    margin-top: 20px;
  }
  &-banner{
    width: 360px;
    @include media($mediapoint-very-small, $mediapoint-large) {
      width: 100%;
      height: 216px;
    }
    &_social{
      background: url("../../images/social.png")0% 0% no-repeat;
      background-size: cover;
      width: 100%;
      max-width: 735px;
      height: 290px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      position: relative;
      margin-bottom: 20px;
      border-radius: 13px;
      @include media($mediapoint-small, $mediapoint-large) {
        width: 100%;
        height: 216px;
        background: url("../../images/social.png") 0% 50% no-repeat;
        background-size: cover;
      }
      @include media($mediapoint-very-small, $mediapoint-small) {

        background: url(../../images/social-banner.svg) 0% 0% no-repeat;
        background-size: 150%;
        height: 275px;
        width: 100%;
        &:before{
          content: '';
          height: 30px;
          width: 100%;
          background-color: white;
          position: absolute;
          top: 0;
          border-bottom-left-radius: 12px;
          border-bottom-right-radius: 12px;
        }
      }
      >div{
        width: 80%;
      }
      &-title{
        text-align: center;
