        font-family: Manrope;
        font-style: normal;
        font-weight: normal !important;
        font-size: 16px  !important;
        line-height: 20px  !important;
        margin: 0;
        /* Achromatic/White */

        color: #FFFFFF  !important;
      }
      &-list-social{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin-top: 20px;
        a{
          background: #F0F0F0;
          width: 48px;
          height: 48px;
          border-radius: 50%;
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          margin: 0px 2% !important;
        }
