    border-radius: 12px;
    min-height: 54px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding: 20px;
    align-items: center;
    @include media($mediapoint-small, $mediapoint-medium) {
      width: 270px;
    }
    @include media($mediapoint-very-small, $mediapoint-small) {
      background: inherit;
      text-align: center;
      padding: 0px 0 20px 0;
      margin: 0 auto;
    }
    &-img {
      background: #F0F0F0;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      margin-right: 10px;
      @include media($mediapoint-very-small, $mediapoint-small) {
        display: none;
      }
    }
    &-content{
      &__title{
        /* Body/Body S Regular */

        font-family: Manrope;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        /* identical to box height, or 133% */
        text-align: left;

        /* Achromatic/Black Primary */
        margin: 0;
        color: #1D1D1B;
        @include media($mediapoint-very-small, $mediapoint-small) {
          text-align: center;
          font-weight: bold !important;
        }
      }
      a{
        /* Body/Body XL Bold */
        margin: 0;

        text-decoration: none;
        font-family: Manrope;
        font-style: normal;
        font-weight: bold;
        font-size: 20px;
        line-height: 28px;
        /* identical to box height, or 140% */

        /* Chromatic/Orange Primary */

        color: #F7A823;
        @include media($mediapoint-very-small, $mediapoint-small) {
          text-align: center;
        }
      }
      &__description{
        margin: 0;
        /* Body/Body M Regular */

        font-family: Manrope;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        /* identical to box height, or 143% */
        text-align: left;
        /* Achromatic/Gray Primary */

        color: #A7A7A7 !important;
        @include media($mediapoint-very-small, $mediapoint-small) {
          text-align: center;
        }
      }
    }
  }

  &_phonenumber.first-phone{
    max-width: 700px;
    @include media($mediapoint-very-small, 1135) {
      background: #FEF4E4 !important;
    }
    .contact-information_phonenumber-img {
      @include media($mediapoint-very-small, 1135) {
        display: flex!important;
      }
    }
    .contact-information_phonenumber-content{
      @include media($mediapoint-very-small, 1135) {
        text-align: left !important;
      }
      &__title{
        @include media($mediapoint-very-small, 1135) {
          text-align: left !important;
          font-weight: bold !important;
        }
      }
      a{
        @include media($mediapoint-very-small, 1135) {
          text-align: left !important;
        }
      }
      &__description{
        @include media($mediapoint-very-small, 1135) {
          text-align: left !important;
        }
      }
    }
  }

  &_mail{
    width: 380px;

    border-radius: 12px;
    height: 64px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding: 20px;
    align-items: center;
    max-width: 700px;
    @include media($mediapoint-very-small, $mediapoint-small ) {
      background-color: inherit;
      text-align: center;
      justify-content: center;
      margin: 0px 0 0 0;
      padding: 0 20px;
      height: auto;
    }
    &-content{
      text-align: left;
      @include media($mediapoint-very-small, $mediapoint-small ) {
        text-align: center;
      }
    }
    &-img {
      background-color: #F0F0F0;
      width: 48px;
      min-width: 48px;
      height: 48px;
      border-radius: 50%;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      margin-right: 10px;
      @include media($mediapoint-very-small, $mediapoint-small ) {
       display: none;
      }
    }
    &-content{
      &__title{
        /* Body/Body S Regular */

        font-family: Manrope;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        /* identical to box height, or 133% */
        text-align: left;

        /* Achromatic/Black Primary */
        margin: 0;
        color: #1D1D1B;
        @include media($mediapoint-very-small, $mediapoint-small) {
          text-align: center;
          font-weight: bold !important;
          margin: 10px;
          font-size: 16px;
          line-height: 20px;
        }
      }
      a{
        /* Body/Body XL Bold */
        margin: 0;

        text-decoration: none;
        font-family: Manrope;
        font-style: normal;
        font-weight: bold;
        font-size: 20px;
        line-height: 28px;
        /* identical to box height, or 140% */

        /* Chromatic/Orange Primary */

        color: #0094AA;
        @include media($mediapoint-very-small, $mediapoint-small) {
          text-align: center;
        }
      }
      &__description{
