        /* Body/Body M Regular */

        font-family: Manrope;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;

        /* Achromatic/Gray Primary */

        color: #A7A7A7 !important;
        @include media($mediapoint-very-small, $mediapoint-small) {
          text-align: center;
          font-size: 12px;
          line-height: 16px;
          margin-top: 10px;

