}

.color-blue{
  color: #0094AA !important;
  font-weight: bold;
}
/*__________parsley__________*/

input.parsley-success,
select.parsley-success,
textarea.parsley-success {
  color: #4F4F4F;
  background-color: #DFF4F7;
  border: 1px solid #DFF4F7 !important;
}

input.parsley-error,
select.parsley-error,
textarea.parsley-error {
  color: #E74C3C;
  background-color: #ffff;
  border: 1px solid #E74C3C !important;
}

.parsley-errors-list {
  margin: 2px 0 3px;
  padding: 0;
  list-style-type: none;
  font-size: 0.9em;
  line-height: 0.9em;
  opacity: 0;
  color: #E74C3C;
}

.parsley-errors-list.filled {
  opacity: 1;
}

.parsley-length {
  color: #F7A823;
}
/*__________parsley__________*/
.error-message{
  background-color: #F9D2CE;
  padding: 15px 40px;
  border-radius: 15px;
  display: none;
  width: auto;
  margin-top: 20px;
  @include media($mediapoint-very-small, $mediapoint-medium) {
    width: auto !important;
  }
  &.active{
    display: block;
  }
  p{
    font-weight: 600;
    font-family: Manrope;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.3px;
    color: #E74C3C;
    margin: 0;
  }
}
.confirm-message{
  background-color: #c4eab5;
  padding: 15px 40px;
  border-radius: 15px;
  width: 100%;
  margin-top: 20px;
  display: none;
  @include media($mediapoint-very-small, $mediapoint-medium) {
    width: auto !important;
  }
  &.active{
   display: block;
  }
  p{
    font-weight: 600;
    font-family: Manrope;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.3px;
    color: #56af31;
    margin: 0;
  }
}
/*____________select____________*/

.select {
  position: relative;
  display: block;
  &:focus{
    outline: none;
  }
}

.select.selected {
  .select__head {
    color: #1D1D1B;
  }
}

.select__head {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  justify-content: space-between;
  padding: 9px 15px;
  padding-right: 35px;
  color: #A7A7A7;
  cursor: pointer;
  background: #FFFFFF;
  border: 1px solid #E0E0E0;
  border-radius: 12px;
  margin-top: 4px;
  font-family: Manrope;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 28px;
  text-align: left;
  position: relative;
  @include media($mediapoint-very-small, $mediapoint-small) {
    text-align: center;
  }
  &_time{
    padding: 14px 15px !important;
  }
  &.open::after {
    transform: translateY(50%) rotate(180deg);
  }

  &::after {
    background-color: transparent;
    padding: 0 13px;
    position: absolute;
    right: 1px;
    top: -2px;
    transform: translateY(50%);
    content: url('../../images/chevron.png');
    display: block;
    transition: .2s ease-in;
  }
}

.select__list {
  display: none;
  position: absolute;
  top: 75px;
  left: 0;
  right: 0;
  background: #fff;
  border-radius: 0 0 10px 10px;
  border: 1px solid #E0E0E0;
  border-top: 0;
  margin-top: 5px;
  max-height: 205px;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 100;
  font-family: Manrope;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 28px;
  text-align: left;
  padding: 0;
/*  &::-webkit-scrollbar {
    width: 7px;
    background-color: #F8F9FA;
    padding: 5px;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #D9D9D9;
  }*/

  .select__item {
    position: relative;
    padding: 10px 15px;
    color: #1D1D1B;
    cursor: pointer;
    list-style-type: none;
    @include media($mediapoint-very-small, $mediapoint-small) {
      text-align: center;
    }
    &:hover {
      background-color: rgba(224, 229, 231, 0.5);
    }
  }
}
