/*
@import "./lib/materialize/materialize-src/sass/materialize.scss";
*/

body {
  margin: 0;
  padding: 0;
  @include media($mediapoint-very-small, $mediapoint-medium) {
    min-width: 300px;
  }
  overflow-x: hidden;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
  position: relative;
  text-align: center;
}

.container-width-100 {
  margin: 0 auto;
  width: 100%;
  position: relative;

  @include media($mediapoint-very-small, $mediapoint-medium) {
    min-width: 320px;
  }
  &.padding-top-header {
    padding-top: 50px;
  }
  &.padding-top-header-100 {
    padding-top: 100px;
  }
}
.container-page {
  margin: 0 auto;
  //width: 100%;
  max-width: 1200px;
  position: relative;
  @include media($mediapoint-medium, $mediapoint-large) {
    margin: 0 25px 0 25px !important;
  }
  @include media($mediapoint-very-small, $mediapoint-medium) {
    min-width: 280px;
  }
}
.bg-grey {
  @include media($mediapoint-very-small, $mediapoint-medium) {
    background: linear-gradient(89.58deg, #4e4e4e 0%, #6d6d6d 88.94%);
  }
  background: inherit;
}

.only-mobile {
  display: none;
  @include media($mediapoint-very-small, $mediapoint-small) {
    display: block !important;
  }
}
.only-desk {
  @include media($mediapoint-very-small, $mediapoint-small) {
    display: none !important;
  }
}
.breakpoint-1 {
  position: absolute;
  top: 70%;
  left: -250px;
}

.modal-bg {
  opacity: 0;
  transition: 0.2s linear;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 10001;
  background: rgba(0, 0, 0, 0.75);
  top: 0;
  left: 0;
  overflow: auto;
  visibility: hidden;

  &.show {
    opacity: 1;
    visibility: visible;
  }

  &.toggle-information-bg {
      display: none;

      @include media($mediapoint-very-small, $mediapoint-medium) {
        display: block;
      }
    }
}

.toggle-modal {
  background-color: #fff;
  border-radius: 12px;
  margin-bottom: 14px;
  transition: 0.2s linear;
  position: fixed;
  top: 50%;
  width: calc(100% - 62px);
  left: 50%;
  padding: 0px 16px 16px 16px;
  z-index: 100003;
  opacity: 0;
  visibility: hidden;
  max-width: 500px;
  transform: translate(-50%, -50%);

  &.show {
    opacity: 1;
    visibility: visible;
  }

  //
  //@include media($mediapoint-small, $mediapoint-medium) {
  //  position: fixed;
  //  width: calc(100% - 84px);
  //  top: 50%;
  //  min-height: 195px;
  //  left: 50%;
  //  transform: translate(-50%, -50%);
  //}
  //
  //@include media($mediapoint-very-small, $mediapoint-small) {
  //  position: fixed;
  //  width: 100%;
  //  min-height: 195px;
  //  bottom: 0;
  //  top: auto;
  //  left: 0;
  //  margin-bottom: 0;
  //  box-sizing: border-box;
  //
  //  //.toggle-information-header {
  //  //
  //  //}
  //}

  .click-information-toggle {
    cursor: pointer;
  }

  &-header {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    margin-top: 15px;

    svg {
      width: 17px;
      cursor: pointer;
      height: 17px;
    }

    .toggle-modal-title {
      font-weight: 700;
      font-size: 16px;
      line-height: 1.3em;
      font-family: 'Manrope';
      color: #1D1D1B;
      margin: 0;
    }
  }

  .toggle-modal-body {
    font-family: 'Manrope';
    font-weight: 400;
    font-size: 14px;
    line-height: 1.4em;
    margin: 10px 0 0 0;
    color: #1D1D1B;

    b {
      margin: 10px 20px;
      display: block;
    }
  }
}


.block-1 {
  height: 700px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background: #ffffff;
  border-radius: 0px 0px 12px 12px;
  // margin-top: 20px;
  @include media($mediapoint-small, $mediapoint-medium) {
    align-items: flex-end;
    justify-content: center;
    height: 100% !important;
    margin-top: 100px;
  }
  @include media($mediapoint-very-small, $mediapoint-small) {
    flex-direction: column;
    height: auto;
    padding-bottom: 0px;
    padding-top: 75px;
  }

  .bg-man {
    position: relative;
    height: 100%;
    width: 50%;

    background: url("../../images/block-1.jpg") 50% 100% no-repeat;
    background-size: 90%;

    @include media(900, $mediapoint-large) {
      background-size: cover;
    }
    @include media($mediapoint-medium, 900) {
      background: none;

      &:before {
        content: "";
        position: absolute;
        min-width: 130%;
        top: 0;
        height: 100%;
        background: url(../../images/block-1.jpg) 50% 100% no-repeat;
        background-size: 100%;
        background-color: transparent;
      }
    }
    @include media($mediapoint-small, $mediapoint-medium) {
      /*width: 65%;
        position: absolute;
        left: -15px;
        z-index: 0;
        top: 0;
        background-size: contain;*/
      display: none;
    }
    @include media($mediapoint-very-small, $mediapoint-small) {
      display: none;
    }
  }
  .calculator-slider {
    width: 50%;
    margin: 0 0 50px 25px;
    padding-top: 0px;

    @include media($mediapoint-medium, $mediapoint-large) {
      margin: 0 9px 0 0;
      width: 50%;
    }
    @include media($mediapoint-small, $mediapoint-medium) {
      width: 80%;
      padding-top: 0px;
      // z-index: 100;
      margin: 20px 15px 25px 0 !important;
    }
    @include media($mediapoint-very-small, $mediapoint-small) {
      width: 90%;
      margin: 10px 0px 0px 0px;

      padding-top: 0px !important;
    }

    .description-service {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      margin: 0 0 20px 0;
      position: relative;
      z-index: 3;
      @include media($mediapoint-very-small, $mediapoint-large) {
        flex-direction: column;
        margin: 0px 0 15px 0;
        align-items: stretch;
      }

      &-box {
        width: 24%;
        background: #f0f0f0;
        border-radius: 12px;
        padding: 24px;

        @include media($mediapoint-very-small, $mediapoint-large) {
          width: auto;
          display: flex;
          flex-direction: row;
          margin: 4px 0;
          padding: 16px;
          height: auto;
          img {
            max-height: 23px;
          }
        }
        p {
          font-family: Manrope;
          font-style: normal;
          font-weight: normal;
          font-size: 16px;
          line-height: 20px;
          color: #1d1d1b;
          margin: 20px 0 0 0;
          @include media($mediapoint-very-small, $mediapoint-large) {
            margin: 0px 0 0px 15px;
            font-size: 14px;
            line-height: 20px;
          }
        }

        img {
          @include media($mediapoint-very-small, $mediapoint-small) {
            max-height: 20px;
          }
        }
      }
    }

    h2 {
      /* Headers/H1 Header */
      position: relative;
      z-index: 3;
      font-family: Manrope;
      font-style: normal;
      font-weight: bold;
      font-size: 28px;
      line-height: 40px;
      display: flex;
      align-items: center;
      color: #56af31;
      margin-bottom: 16px;
      @include media($mediapoint-small, $mediapoint-medium) {
        text-align: left;
        margin: 10px 0;
      }
      @include media($mediapoint-very-small, $mediapoint-small) {
        justify-content: center;
        font-size: 20px;
        margin: 10px 0px 0px 0px;
        line-height: 1.4em;
      }
    }
    p.title {
      /* Body/Body L Bold */
      position: relative;
      z-index: 3;
      font-family: Manrope;
      font-style: normal;
      font-weight: bold;
      font-size: 20px;
      line-height: 28px;
      margin-top: 0;

      color: #1d1d1b;
      @include media($mediapoint-small, $mediapoint-medium) {
        text-align: left;
        margin: 10px 0;
      }
      @include media($mediapoint-very-small, $mediapoint-small) {
        text-align: center;
        font-size: 16px;
        line-height: 1.3em;
        margin: 13px 0;
      }
    }
    &-box {
      max-width: 660px;
      height: 462px;
      background: #e3f7da;
      border-radius: 12px;
    }
    .calculator {
      background-color: #e3f7da;
      padding: 16px;
      border-radius: 12px;
      position: relative;

      @include media($mediapoint-small, $mediapoint-medium) {
        position: static;
      }

      .click-information-toggle {
        cursor: pointer;
      }

      .toggle-information {
        background-color: #fff;
        border-radius: 12px;
        margin-bottom: 14px;
        min-height: 234px;
        transition: 0.2s linear;
        position: absolute;
        top: 14px;
        width: calc(100% - 62px);
        left: 16px;
        padding: 0px 16px 16px 16px;
        z-index: 100033;
        opacity: 0;
        visibility: hidden;

        &.show {
          opacity: 1;
          visibility: visible;
        }

        @include media($mediapoint-small, $mediapoint-medium) {
          position: fixed;
          width: calc(100% - 84px);
          top: 50%;
          min-height: 195px;
          left: 50%;
          transform: translate(-50%, -50%);
        }

        @include media($mediapoint-very-small, $mediapoint-small) {
          position: fixed;
          width: 100%;
          min-height: 195px;
          bottom: 0;
          top: auto;
          left: 0;
          margin-bottom: 0;
          box-sizing: border-box;

          .toggle-information-header {
            svg {
              width: 20px;
              height: 20px;
            }
          }
        }

        &-header {
          display: flex;
          justify-content: space-between;
          flex-direction: row;
          align-items: center;
          margin-top: 15px;
        }
        p {
          /* Body/Body M Regular */

          font-family: Manrope;
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 20px;
          color: #1d1d1b;
        }
      }

      .calculator__sum {
        background-color: #fff;
        border-radius: 12px;
        padding: 0 16px 10px 16px;
        margin-bottom: 14px;
        // height: 96px;
        transition: background 1s linear 0s;
        &.blocked {
          cursor: wait;
          background-color: #f5fbf3 !important;
          #form-sum,
          #form-day {
            pointer-events: none;
          }
        }
        #calculator__sum,
        #calculator__day {
          font-size: 16px;
        }

        #calculator__sum {
          margin-left: 10px;
        }

        .calculator__sum__text-top {
          display: flex;
          justify-content: space-between;

          p.body_m-bold {
            font-family: Manrope;
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            line-height: 20px;
            color: #4f4f4f;
            @include media($mediapoint-very-small, $mediapoint-small-360) {
            }
          }
          p.body_l-bold {
            font-family: Manrope;
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            line-height: 20px;
            color: #56af31;
            white-space: nowrap;

            @include media($mediapoint-very-small, $mediapoint-small-360) {
            }
          }
        }

        .calculator__sum__text-bottom {
          display: flex;
          justify-content: space-between;
          color: #a7a7a7;
          margin-top: 10px;
          p {
            font-family: Manrope;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 20px;
            margin: 0;
          }
        }
      }

      .calculator__rainbow {
        border-radius: 12px;
        padding: 0 5px;
        margin-bottom: 14px;
        height: 135px;
        transition: background 1s linear 0s;
        position: relative;
        @include media($mediapoint-medium, $mediapoint-medium-large) {
          height: 135px !important;
        }
        @include media($mediapoint-very-small, $mediapoint-medium) {
          padding: 0px !important;
        }
        p.title {
          font-family: Manrope;
          font-style: normal;
          font-weight: bold;
          font-size: 16px;
          line-height: 28px;
          margin: 15px 0;
          color: #1d1d1b;
          @include media($mediapoint-very-small, $mediapoint-medium) {
            font-size: 14px;
            line-height: 20px;
          }
        }
        &-line {
          height: 8px;
          background: linear-gradient(90deg, #e74c3c 0%, #f7a823 29.79%, #f7d62f 50.78%, #5cf73b 70.51%, #56af31 100%);
          border-radius: 4px;
          margin: 15px 0;
          margin: 15px 0 5px 0;
        }

        &-numbers {
          display: flex;
          justify-content: space-between;
          flex-direction: row;
          &-box {
            position: relative;
            display: flex;
            justify-content: space-between;
            flex-direction: row;
            align-items: center;
            min-width: 91px;
            max-width: 160px;
            height: 36px;
            padding: 0 10px;
            background: #d0f1c1;
            border-radius: 5px;
          }
          &-info {
            p {
              margin: 0;
              font-size: 12px !important;
              line-height: 16px !important;
              font-weight: normal !important;
            }
          }
          &-wrong {
            p {
              font-family: Manrope;
              font-style: normal;
              font-weight: bold;
              font-size: 14px;
              line-height: 20px;
              color: #e74c3c;
              margin-right: 5px;
            }
            &#wrong-number-rainbow:before {
              content: "";
              position: absolute;
              left: 0px;
              bottom: -6px;
              border: 7px solid transparent;
              border-left: 13px solid #d0f1c1;
            }
          }
          &-good {
            p {
              font-family: Manrope;
              font-style: normal;
              font-weight: bold;
              font-size: 14px;
              line-height: 20px;
              margin-right: 5px;
              color: #56af31;
            }
            &#good-number-rainbow:before {
              content: "";
              position: absolute;
              right: 0px;
              bottom: -6px;
              border: 7px solid transparent;
              border-right: 13px solid #d0f1c1;
            }
          }
        }
        .calculator__sum__text-bottom {
          display: flex;
          justify-content: space-between;
          color: #a7a7a7;
          margin-top: 10px;
          p {
            font-family: Manrope;
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 16px;
            margin: 0;
          }
        }
      }

      .calculator__info {
        & button {
          color: #56af31;
          border: none;
          -webkit-tap-highlight-color: transparent;
          background-color: transparent;
          cursor: pointer;
          font-family: Manrope;
          font-style: normal;
          font-weight: bold;
          font-size: 20px;
          line-height: 28px;
          text-align: center;
          width: 100%;
          height: 35px;
          @extend %d-flex-row;
          @extend %d-flex-center-center;
          &.active {
            color: #56af31;
            svg path {
              fill: #56af31;
            }
          }
          svg {
            margin-left: 5px;
            path {
              fill: #56af31;
            }
          }
          &:hover {
            color: #56af31;
            svg path {
              fill: #56af31;
            }
          }
        }

        & p {
          margin-top: 16px;
          margin-bottom: 4px;
          font-family: Manrope;
          font-style: normal;
          font-weight: bold;
          font-size: 14px;
          line-height: 20px;
          text-align: center;
        }

        & div {
          .more-visiable {
            li {
              font-size: 14px;
              font-weight: bold;
              color: #3f8024;
            }
            .body_m-bold {
              color: #3f8024;
            }
          }
          & ul {
            display: flex;
            justify-content: space-between;
            margin: 0 0 6px 0;
            padding: 0;
            .body_m-bold {
              color: #56af31;
              width: 30%;
              min-width: 80px;
              text-align: right;
              font-weight: bold !important;
            }

            & li {
              list-style-type: none;
              font-family: Manrope;
              font-style: normal;
              font-weight: normal;
              font-size: 14px;
              line-height: 20px;
              color: #4f4f4f;
              > span {
                font-size: 12px;
                line-height: 16px;
                color: #a7a7a7;
              }
            }
          }
        }
      }

      .calculator_column-left {
        border-right: 1px solid #fff;
        padding-right: 16px;
        margin-right: 16px;
        width: 50%;
        min-height: 80px;
        @include media($mediapoint-very-small, $mediapoint-medium-large) {
          width: 100%;
          padding-right: 0px;
          margin-right: 0px;
          min-height: 60px !important;
          height: auto;
          border-right: 0px solid #fff !important;
        }
      }
      .calculator_column-right {
        width: 50%;
        min-height: 80px;
        @include media($mediapoint-very-small, $mediapoint-medium-large) {
          width: 100%;
          min-height: 60px !important;
          height: auto;
        }
      }

      .calculator__button {
        margin: 15px 0;
        -webkit-tap-highlight-color: transparent;
        text-align: center;
        & a {
          & img {
            width: 20px;
            height: 20px;
            margin-right: 10px;
            margin-top: -3px;
          }
        }
      }
    }

    /* _________accordion_____________ */

    .button-amount {
      -webkit-tap-highlight-color: transparent;
      .fa-chevron-up {
        transform: rotate(180deg);
        transition: 0.3s ease-out;
      }
    }

    .amount-detalis {
      height: 0;
      overflow: hidden;
      transition: height 0.3s ease-out;
      margin-top: 8px;
      @extend %d-flex-row;
      justify-content: flex-start;
      align-items: flex-start;
      @include media($mediapoint-very-small, $mediapoint-medium-large) {
        flex-direction: column !important;
        &.active {
          height: auto !important;
        }
      }
      &.active {
        height: 140px;
      }
    }
  }
}
.green-button {
  background-color: $green;
  color: #fff !important;
  box-shadow: 0px 4px 14px rgba(80, 163, 46, 0.32);
  border-radius: 24px;
  padding: 12px 38px;
  cursor: pointer;
  font-family: Manrope;
  font-style: normal;
  font-weight: 800;
  font-size: 16px;
  line-height: 20px;
  text-decoration: none;
  border: none;
  max-width: 186px;
  @extend %d-flex-row;
  @extend %d-flex-center-center;
  width: 50%;
  min-width: 150px;
  margin: 0 auto;
  -webkit-tap-highlight-color: transparent;
  @include media($mediapoint-very-small, $mediapoint-medium) {
    width: auto;
    max-width: 180px;
    &.w-100 {
      width: 100% !important;
      text-align: center;
    }
  }
  &:hover {
    background-color: $green-hover;
  }
  &:focus {
    background-color: $green-action;
  }
  img {
    margin-right: 10px;
  }
}
.black-button {
  background-color: #1d1d1b;
  color: #fff !important;
  border-radius: 24px;
  padding: 12px 38px;
  cursor: pointer;
  font-family: Manrope;
  font-style: normal;
  font-weight: 800;
  font-size: 16px;
  line-height: 20px;
  text-decoration: none;
  border: none;
  max-width: 236px;
  @extend %d-flex-row;
  @extend %d-flex-center-center;
  width: 100%;
  min-width: 150px;
  margin: 0 auto;
  -webkit-tap-highlight-color: transparent;
  @include media($mediapoint-very-small, $mediapoint-medium) {
    width: auto;
    max-width: 180px;
    &.w-100 {
      width: 100% !important;
    }
  }
  &:hover {
    background-color: #404040;
  }
  &:focus {
    background-color: #000000;
  }
  img {
    margin-right: 10px;
  }
}
.orange-button-event {
  background: #f7a823;
  box-shadow: 0px 4px 12px rgba(247, 168, 35, 0.48);
  color: #fff !important;
  border-radius: 24px;
  padding: 12px 0px;
  cursor: pointer;
  font-family: Manrope;
  font-style: normal;
  font-weight: 800;
  font-size: 16px;
  line-height: 20px;
  text-decoration: none;
  border: none;
  @extend %d-flex-row;
  @extend %d-flex-center-center;
  width: calc(100% - 30px);
  min-width: 150px;
  margin: 0 15px;
  text-align: center;
  -webkit-tap-highlight-color: transparent;
  height: 23px;
  @include media($mediapoint-very-small, $mediapoint-medium) {
  }
  &:hover {
    background-color: #404040;
  }
  &:focus {
    background-color: #000000;
  }
  img {
    margin-right: 10px;
  }
  &.abs-position-button {
    position: absolute;
    bottom: 40px;
  }
}
.block-2 {
  min-height: 320px;
  background: linear-gradient(89.58deg, #4e4e4e 0%, #6d6d6d 88.94%);

  &-main-container {
    position: relative;
    margin: 0 0 0 5px;
    @include media($mediapoint-medium, $mediapoint-large) {
      margin: 0 0 0 45px;
    }
    @include media($mediapoint-small, $mediapoint-medium) {
      margin: 0 0 0 45px;
    }
    @include media($mediapoint-very-small, $mediapoint-small) {
      margin: 0 0 0 0px;
    }
  }

  .abs-posotion-img {
    position: absolute;
    bottom: 0;
    right: -35px;
    width: 335px;
    z-index: 0;
    @include media($mediapoint-medium, $mediapoint-large) {
      right: 0px;
      width: 435px;
    }
    @include media($mediapoint-small, $mediapoint-medium) {
      right: 0px;
      width: 350px;
    }
    @include media($mediapoint-very-small, $mediapoint-small) {
      position: relative;
      max-width: 500px;
      right: 15px !important;
      width: 100%;
    }
  }

  &-main-container {
    padding-top: 20px;
