.point-menu-active {
  position: absolute;
  height: 47px;
  width: 50%;
  top: 0;
  left: 0;
  z-index: 0;
  background: #56af31;
  /* Button/Switcher */
  box-shadow: 0px 2px 4px 1px rgba(49, 49, 49, 0.08);
  border-radius: 24px;
  border: 1px solid #eaeaea;
  transition: all 0.4s ease-in-out;
  z-index: 0;
  &[data-active="left"] {
    left: 0;
  }
  &[data-active="middle"] {
    left: calc(50% - 2px);
  }

  @include media($mediapoint-very-small, $mediapoint-small) {
    height: 38px;
    border: none;

    &[data-active="left"] {
      left: 0;
    }
    &[data-active="middle"] {
      left: 50%;
    }
  }
}


