.circle-menu{
  position: fixed;
  list-style: none;
  margin:0 0 ;
  padding: 0 0;
  bottom: 35px;
  right: 10px;
  width: 70px;
  height: 70px;
  z-index: 999;

  span {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius:50%;
    cursor: pointer;
    z-index:2;
    left: 0;
    bottom: 0;
    transform: translate(-25%, 25%);
    transition: 0.3s;
    user-select: none;
    background: url(../../images/circle-menu.svg) 50% 50% / 40% no-repeat;
    background-color: #42AB44;

    &:hover {
      background-color: #245F34;
    }

    &.active-menu {
      background-color: #245F34;
      background-image: url(../../images/circle-menu-close.svg);
    }

    img {
      width: 40%;
      height: 40%;
    }
  }

    li{
      position: absolute;
      width: 35px;
      height: 35px;
      overflow: hidden;
      border-radius:50%;
      background: teal;
      z-index: 1;
      cursor: pointer;
      bottom: 0;
      left: 0;
      transition: 0.3s;
    }
}

